@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Share+Tech+Mono&display=swap);
.switchContainer{display:flex;align-items:center;margin-bottom:20px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.switchContainer .switch{position:relative;display:inline-block;width:calc(20px * 2 + 4px * 2);height:calc(20px + 4px * 2);margin-right:15px}.switchContainer .switch input{opacity:0;width:0;height:0}.switchContainer .switch input:checked+.slider{background-color:#089806}.switchContainer .switch input:checked+.slider:before{transform:translateX(20px)}.switchContainer .switch input:focus+.slider{box-shadow:0 0 1px #089806}.switchContainer .switch .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#9f9f9f;transition:.4s}.switchContainer .switch .slider:before{position:absolute;content:"";height:20px;width:20px;left:4px;bottom:4px;background-color:#fff;transition:.4s}.switchContainer .switch .slider:hover{background-color:#727272}
body{margin:0}.app{font-family:"Share Tech Mono",monospace;font-size:1.25rem;padding:60px 40px;display:flex;flex-direction:column;align-items:center}.app h1,.app h2{font-family:"Press Start 2P",monospace}.app h1{font-size:2.5rem;line-height:3.5rem}.app h2{font-size:1.5rem;line-height:2.5rem}.app .header{display:flex;flex-direction:column;align-items:center;margin-bottom:40px;text-align:center}.app .header .logo{width:160px;margin-bottom:10px}.app .section{max-width:100%}.app .section:not(:last-child){margin-bottom:60px}.app .tableContainer{width:100%;overflow:auto}.app table{text-align:left;border-spacing:0;margin:auto}.app table thead{font-size:1.5rem}.app table th,.app table td{padding:10px 40px 10px 10px;border-bottom:1px solid #9f9f9f}.app table th:last-child,.app table td:last-child{padding-right:10px}.app table th.points,.app table td.points{padding-left:10px}.app table th.medal,.app table td.medal{padding-right:0}.app table th.alignCenter,.app table td.alignCenter{text-align:center}.app table th.small,.app table td.small{font-size:1.25rem}.app table th{white-space:nowrap}.app .leaderboard{font-size:1.5rem}.app .leaderboard .playerName{display:flex;align-items:center}.app .leaderboard .playerName .avatar{height:35px;border-radius:100%;margin-right:10px;border:1px solid #000}.app .challenges tbody tr:hover:not(.completed){background-color:#fff5ca;transition:ease .25s;font-weight:bold}.app .challenges tbody tr.completed{color:#9f9f9f}.app .challenges tbody tr.completed .icon{-webkit-filter:brightness(0.4);filter:brightness(0.4)}.app .challenges tbody tr td{padding-top:5px;padding-bottom:5px}.app .challenges tbody tr td .challengeDetails{display:flex;align-items:center}.app .challenges tbody tr td .challengeDetails .icon{width:45px;height:45px;border-radius:6px;display:block;border:1px solid #000;margin-right:15px}.app .countdown{display:flex;font-size:1.75rem;text-align:center}.app .countdown .timeItem{margin-right:40px}.app .countdown .timeItem:last-child{margin-right:0}.app .countdown .time{font-weight:bold;font-size:2.25rem;font-family:"Press Start 2P",monospace;margin-bottom:10px}.app .countdown .customStatus,.app .countdown .time{color:#c80405}.loading{color:#2684e6}@media screen and (max-width: 768px){.app h2{text-align:center}.app .leaderboard,.app table thead,.app table td.small{font-size:1.25rem}.app .switchContainer{margin-left:auto;margin-right:auto}}@media screen and (max-width: 576px){.app{padding:20px;font-size:1rem}.app h1{font-size:2rem;line-height:3rem}.app h2{font-size:1rem;line-height:2rem}.app .header .logo{width:130px}.app .section:not(:last-child){margin-bottom:40px}.app .leaderboard,.app table thead,.app table td.small{font-size:1rem}.app .countdown{font-size:1.25rem}.app .countdown .timeItem{margin-right:20px}.app .countdown .time{font-size:1.5rem}}
